import React, {useMemo, useState} from "react";
import Modal from "react-bootstrap/Modal";
import DataTable from 'react-data-table-component';
import FilterComponent from "../../agents/FilterComponent";
import {errorToast, getCookieValue, ipAddress, tokenName} from "../../../../utils/Common";
import axios from "axios";

const UsersTable = (props) => {
  const columns = [
    {
      name: "Name",
      selector: row => row.FirstName + " " + row.LastName,
      sortable: true,
      // width: '160px'
    },
    {
      name: "User Name",
      selector: row => row.UserName,
      sortable: true,
      // width: '160px'
    },
    {
      name: "Email",
      selector: row => row.Email,
      sortable: true,
      width: '250px'
    },
    {
      name: "Phone Number",
      selector: row => row.PhoneNumber,
      sortable: true,
      // width: '160px'
    },
    // {
    // name: "Edit",
    // button: true,
    // width: '80px',
    // cell: row => <>
    //   <button onClick={() => props.click(row.UserId)} className="btn btn-link text-decoration-none">
    //     Edit
    //   </button>
    // </>
    // },
    {
      name: "Details",
      button: true,
      cell: row => <>
        <button onClick={() => ReadDetails(row.Id)} className="btn btn-link text-decoration-none">
          Details
        </button>
      </>
    },
    // {
    //   name: "Delete",
    //   button: true,
    //   cell: row => <>
    //     <button onClick={() => props.click(row.UserId)} className="btn btn-link text-decoration-none">
    //       Edit
    //     </button>
    //   </>
    // }
  ];

  const [filterText, setFilterText] = React.useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = React.useState(false);

  const filteredItems = props.data.filter(
    item => JSON.stringify(item).toLowerCase().indexOf(filterText.toLowerCase()) !== -1
  );

  const subHeaderComponent = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <>
        <FilterComponent
          onFilter={e => setFilterText(e.target.value)}
          onClear={handleClear}
          filterText={filterText}
        />
      </>
    );
  }, [filterText, resetPaginationToggle]);

  /* ---------------------------------------- */
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [user, setUser] = useState(null);

  async function ReadDetails(Id) {
    try {
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/users/" + Id

      let res = await axios.get(url, {
        headers: {'Authorization': `Bearer ${token}`}
      })
      // console.log(res)
      // console.log(res.data)

      if (res.status === 200 && res.data.error) {
        errorToast(res.data.message)
      } else if (res.status === 200 && !res.data.error) {
        console.log(res.data.recordset[0])
        setUser(res.data.recordset[0])
        handleShow();
      }
    } catch (e) {
      errorToast(e.message)
    }
  }

  return (
    <>
      <DataTable
        columns={columns}
        data={filteredItems}
        striped
        pagination
        subHeader
        subHeaderComponent={subHeaderComponent}
      />

      <Modal show={show} onHide={handleClose} centered size="lg">
        {/*<Modal show={show} onHide={handleClose} centered fullscreen>*/}
        <Modal.Header closeButton>
          <Modal.Title>User Details</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <div className="col-12 mb-3">
              <p className="mb-0">
                User Status:
                {/*User Status: <span className="badge bg-success text-white">Active</span>*/}
              </p>
            </div>

            <div className="col-2 mb-3">
              <p className="mb-0">Role:</p>
            </div>

            <div className="col-10 mb-3">
              {user?.roles?.map(x => <p className="mb-0" key={x.Name}>{x.Name}</p>)}
            </div>

            <div className="col-md-6 mb-3">
              <p className="mb-0">First Name: <b>{user?.FirstName}</b></p>
            </div>

            <div className="col-md-6 mb-3">
              <p className="mb-0">Last Name: <b>{user?.LastName}</b></p>
            </div>

            <div className="col-md-6 mb-3">
              <p className="mb-0">Email: <b>{user?.Email}</b></p>
            </div>

            <div className="col-md-6 mb-3">
              <p className="mb-0">Mobile No: <b>{user?.PhoneNumber}</b></p>
            </div>

            <div className="col-md-6 mb-3">
              <p className="mb-0">UserName: <b>{user?.UserName}</b></p>
            </div>

            <div className="col-md-6 mb-3">
              <p className="mb-0">
                Manager Name: <b>{user?.manager?.FirstName} {user?.manager?.LastName}</b>
              </p>
            </div>

            <div className="col-md-6 mb-3">
              <p className="mb-0">Branch: <b>{user?.BranchName}</b></p>
            </div>

            <div className="col-md-6 mb-3">
              <p className="mb-0">Department: <b>{user?.DepartmentName}</b></p>
            </div>
          </div>
          <hr/>
          <p className="">LockoutEnabled</p>
          <p className="mb-0">CreatedOn</p>
        </Modal.Body>
      </Modal>
    </>
  )
}
export default UsersTable;
